<template>
  <div class="vw-100 vh-100 d-flex justify-content-center align-items-center position-relative" ref="page404">
    <canvas ref="test" />
    <div class="position-absolute card px-5 py-5 h4 gradient" ref="title">Page not found - 404</div>
  </div>
</template>
<script>
export default {
  mounted() {
    this.worker = new Worker("../service/waterDrops.js",{ type: "module" });
    let title = this.$refs.title;
    let titleObjectDimensions = title.getBoundingClientRect();
    let titleObj = {
      width: titleObjectDimensions.width,
      height: titleObjectDimensions.height,
      x: title.offsetLeft,
      y: title.offsetTop,
    };
    let ccanvas = this.$refs.test;
    ccanvas.width = this.$refs.page404.getBoundingClientRect().width;
    ccanvas.height = this.$refs.page404.getBoundingClientRect().height;

    let rtcanvas = ccanvas.transferControlToOffscreen();
    this.worker.postMessage({ canvas: rtcanvas, domObj: titleObj }, [rtcanvas]);
  },
  data() {
    return {
      worker: null,
    };
  },
};
</script>